#mainpane-wrapper {
    flex: auto;    
}
#mainpane-wrapper > div {
    display: flex;
    flex-direction: column;
}
#mainpane-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 70px;
    padding: 0px 20px;
    box-shadow: 1px 2px 1px #f2f2f2;
    flex-shrink: 0;
}
#app-name {
    display: flex;
    align-items: center;
    color: gray;
    font-weight: 500;
    font-size: 20px;
    font-family: serif;    
}
#app-name > span {
    margin: 0px 5px 0px 25px;
}
#app-menu {
    display: flex;
    justify-content: flex-end;    
}
#app-menu > div {
    margin: 0px 10px;
    font-family: Calibri;
    font-size: 17px;
    font-weight: 500;
    cursor: pointer;
    color: gray;
}
#app-menu > div:hover {
    color: #817d7d;
    text-decoration: underline;
}
#mainpane-subheader {
    display: flex;
    flex-direction: column;    
    transition: height 0.5s;
    width: 100%;
    box-sizing: border-box;
    flex-shrink: 0;
}
#mainpane-subheader.open {
    height: 170px;
}
#mainpane-subheader.close {
    height: 0px;;
}
#question-suggestion-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 20px;
    box-sizing: border-box;
    height: 100%;
}
#question-suggestion-wrapper > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    height: 100%; 
}
#question-suggestion-icon {
    width: 10%;
}
#question-suggestions {
    width: 90%;
}
#question-suggestion-wrapper > div > div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    height: 100%;
    box-sizing: border-box;
    border-radius: 5px;
    padding: 10px;
    margin-right: 20px;
    border: 1px solid #e5e5e5;
    font-size: 14px;
    font-weight: 500;
    overflow: hidden;
    color: #414141;
    box-shadow: 1px 1px 9px #e1e1e1;
    text-align: center;    
}
#question-suggestion-wrapper > div > div:hover {
    background-color: #dfdfdf;
    cursor: pointer;
}
#question-suggestion-icon {
    padding-right: 20px;
}
#mainpane-chatwindow {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    flex: 1;
}